<template>
  <div class="seePage">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>库存管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">库存盘点</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看库存盘点</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" :model="TableData" class="demo-form-inline">
        <el-form-item label="库存组织：" size="mini">
          {{TableData.instoragList.company_name}}
        </el-form-item>
        <el-form-item label="单据号：" size="mini">
          {{TableData.instoragList.stock_taking_sn}}
        </el-form-item>
        <el-form-item label="业务日期：" size="mini">
          {{TableData.instoragList.business_time}}
        </el-form-item>

        <el-form-item label="仓库名称：" size="mini">
          {{TableData.instoragList.warehouse_name}}
        </el-form-item>
        <el-form-item label="货位：" size="mini">
          {{TableData.instoragList.host_allocation_name}}
        </el-form-item>
        <el-form-item label="盘盈入库单号：" size="mini">
          {{TableData.instoragList.instorage_sn}}
        </el-form-item>
        <el-form-item label="盘亏出库单号：" size="mini">
          {{TableData.instoragList.outstock_sn}}
        </el-form-item>
        <el-form-item label="商品类型：" size="mini">
          {{TableData.instoragList.goods_type_name}}
        </el-form-item>
        <el-form-item label="品牌名称：" size="mini">
          {{TableData.instoragList.brand_name}}
        </el-form-item>
        <el-form-item label="商品名称：" size="mini">
          {{TableData.instoragList.host_goods_name}}
        </el-form-item>
        <el-form-item label="库管员：" size="mini">
          {{TableData.instoragList.keeper_name}}
        </el-form-item>
        <el-form-item label="审核状态：" size="mini">
          {{TableData.instoragList.auditor_status_name}}
        </el-form-item>
        <el-form-item label="备注：" size="mini">
          {{TableData.instoragList.remark}}
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.goodsList" style="width: 100%" max-height="600" :summary-method="getSummaries" show-summary>
        <el-table-column :show-overflow-tooltip="true" type="index" align="center" label="序号">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" label="年周/批次">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="unit_name" label="单位">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="货位名称">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="stocks" label="账面数量" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="real_stocks" label="实盘数量" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="stocks_num" label="盘盈/亏数量" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="is_entity" min-width="160" label="是否为实物库存">
        </el-table-column>
      </el-table>
    </div>
    <div class="refundauditFoot">
      <span v-if="TableData.instoragList.update_user_name!=''">修改人：{{TableData.instoragList.update_user_name}}</span>
      <span v-else>创建人：{{TableData.instoragList.add_user_name}}</span>
      <span v-if="TableData.instoragList.update_time!=0">修改时间：{{TableData.instoragList.update_time}}</span>
      <span v-else>创建时间：{{TableData.instoragList.add_time}}</span>
    </div>

    <!-- 打印组件 -->
    <div>
      <takestockPrint ref="printRef"></takestockPrint>
    </div>

    <div class="dialog-footer" style="text-align:center;margin-top:20px;">
      <!-- 未审核 -->
      <template v-if="TableData.instoragList.auditor_status == 1">
        <el-button type="primary" @click="audit('auditorPassTakeStock')" v-if="authority.b_pass==1">审核</el-button>
        <el-button @click="audit('auditorInvalidTakeStock')" v-if="authority.b_invalid==1">作废</el-button>
      </template>
      <!-- 作废 -->
      <template v-if="TableData.instoragList.auditor_status == 2 || TableData.instoragList.auditor_status == 3 || (authority.b_invalid != 1 && authority.b_pass != 1)">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>取消</el-button>

      <template v-if="takestockSeeGet.after_auditor_print_status == 1">
        <el-button @click="printF" type="primary" v-if="TableData.instoragList.auditor_status == 2">打印</el-button>
      </template>
      <template v-else>
        <el-button @click="printF" type="primary">打印</el-button>
      </template>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import takestockPrint from "common/components/public/takestockPrint.vue"; //打印组件
import { repertory } from "common/utils/public_function/commonUtils";
export default {
  name: "instorageTakestockTakestockSee",
  data() {
    return {
      hasAuditorStatusPrint: null,
      authority: {},
      TableData: {
        instoragList: {},
      },
      params: {},
      per_page: [30, 50, 100, 500],
      goPage: 1,
      pageSize: 30,
    };
  },
  components: {
    takestockPrint,
  },
  computed: {
    ...mapGetters(["takestockIndexGet", "takestockSeeGet"]),
  },
  methods: {
    ...mapActions(["takestockIndexACT"]),
    goBack() {
      this.$router.push({
        path: "/inventory/takestock",
      });
      this.takestockIndexACT({
        goPage: this.takestockSeeGet.goPage,
        params: this.takestockSeeGet.params,
        pageSize: this.takestockSeeGet.pageSize,
      });
    },
    printF() {
      this.$nextTick(() => {
        this.$refs.printRef.printPdf(this.TableData);
      });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "合计";
          return;
        }
        if (["stocks", "real_stocks", "stocks_num"].includes(column.property)) {
          const values = data.map((item) => Number(item[column.property]));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
        }
      });
      return sums;
    },
    audit(api) {
      let vm = this;
      if (api == "auditorPassTakeStock") {
        // 校验安全库存金额
        this.$axMethod
          .safetyStockCheck({
            bill_logogram: "pkck",
            order_type: 2,
            bill_id: this.params.stock_taking_id,
          })
          .then((data) => {
            this.$axios({
              method: "post",
              url: this.$api[api],
              data: {
                stock_taking_id: this.params.stock_taking_id,
              },
            }).then((res) => {
              if (res.data.code == 200) {
                this.$message({
                  message: res.data.msg ? res.data.msg : res.data.data,
                  type: "success",
                });
                this.goBack();
              }
            });
          });
      } else {
        this.$axios({
          method: "post",
          url: this.$api[api],
          data: {
            stock_taking_id: this.params.stock_taking_id,
          },
        }).then((res) => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.msg ? res.data.msg : res.data.data,
              type: "success",
            });
            this.goBack();
          }
        });
      }
    },
    initData() {
      this.$axMethod.infoTakeStock(this.params).then(({ data }) => {
        if (data.code == "200") {
          this.TableData = data.data;
        }
      });
    },
  },
  created() {
    this.hasAuditorStatusPrint = window.localStorage.hasAuditorStatusPrint;
    this.authority = JSON.parse(localStorage.getItem("authority"));
    if (this.takestockSeeGet.stock_taking_id) {
      this.params = {
        stock_taking_id: this.takestockSeeGet.stock_taking_id,
      };
      this.initData();
    }
  },
};
</script>
