<template>
  <!-- 盘点单打印 -->
  <div v-show="false">
    <div v-if="params!=null">
      <!-- <div id="header" v-if="params!=null"> -->
      <div id="print-header">
        <table class="header" cellpadding="0" cellspacing="0" style="width: 100%;">
          <tr>
            <td colspan="2">
              <h2 style="text-align:center; padding-bottom:0px;font-size: 13.5pt;line-height:24pt;margin:0px">盘点单</h2>
            </td>
          </tr>
          <tr></tr>
          <tr>
            <td>
              <b>单据日期:</b>{{TableData.instoragList.business_time}}</td>
            <td colspan="2">
              <b>库存组织:</b>{{TableData.instoragList.company_name}}</td>
          </tr>
          <tr>
            <td colspan="2">
              <b>仓库名称:</b>{{TableData.instoragList.warehouse_name}}</td>
            <td>
              <b>库管员:</b>{{TableData.instoragList.keeper_name}}</td>
          </tr>

        </table>
      </div>
      <div id="print-body">
        <table class="body" cellpadding="0" cellspacing="0" style="width: 100%;">
          <thead>
            <tr>
              <th width="8%">序号</th>
              <th width="10%">商品编号</th>
              <th width="25%" style="word-break: break-all">商品名称</th>
              <th width="10%">年周/批次</th>
              <th width="12%">货位名称</th>
              <th width="10%">账面数量</th>
              <th width="10%">实盘数量</th>
              <th width="15%">备注</th>
            </tr>
          </thead>
          <tbody>
            <!-- 这里开始循环 -->
            <tr v-for="(item,index) in TableData.goodsList" :key="index">

              <td style="text-align:center">{{index+1}}</td>
              <td>{{item.goods_id}}</td>
              <td>{{item.goods_name}}</td>
              <td>{{item.batch}}</td>
              <td>{{item.allocation_name}}</td>
              <td style="text-align:right">{{item.stocks}}</td>
              <td style="text-align:right">{{item.real_stocks}}</td>
              <td>{{item.remark}}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td style="text-align:center" colspan="2">本页小计</td>
              <td></td>
              <td></td>
              <td></td>
              <td tdata="subSum" format="0" style="text-align:right">
                <font color="#0000FF" v-if="stocksSum"> ### </font>
                <font color="#0000FF" v-else></font>
              </td>
              <td tdata="subSum" format="0" style="text-align:right">
                <font color="#0000FF" v-if="stocksSum">###</font>
                <font color="#0000FF" v-else></font>
              </td>
              <td>
              </td>
            </tr>
            <tr>
              <td style="text-align:center" colspan="2">总计</td>
              <td></td>
              <td></td>
              <td></td>
              <td tdata="subSum" format="0" style="text-align:right">
                <font color="#0000FF">{{stocksSum}}</font>
              </td>
              <td tdata="subSum" format="0" style="text-align:right">
                <font color="#0000FF">{{realStocksSum}}</font>
              </td>
              <td>
              </td>
            </tr>
          </tfoot>
        </table>
      </div>
      <div id="print-pages">
        <p style='text-align:center;font-size:9pt;'>
          页码：
          <span tdata='pageNO'>第 ## 页</span> /
          <span tdata='pageCount'>共 ## 页</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { getLodop } from "common/utils/LodopFuncs";
import {
  getInventoryFinanceCompanyOpen,
  getEmployeeListCommon
} from "common/utils/public_function/commonUtils";
let LODOP = getLodop;
export default {
  data() {
    return {
      params: null,
      warehouseList: {
        options: []
      },
      companyList: [],
      getEmployeeList: {
        options: []
      },
      employeeList: {
        options: []
      },
      TableData: {},
      stocksSum: null,
      realStocksSum: null
    };
  },
  methods: {
    list(data) {
      let _this = this;
      this.params = data;
      this.$set(this.TableData, "instoragList", data.instoragList);
      let datalen = data.goodsList.length;
      if (datalen) {
        try {
          this.stocksSum = data.goodsList
            .filter(obj => obj.stocks)
            .map(obj => obj.stocks)
            .reduce((total, next) => {
              return Number(total) + Number(next);
            }, 0); // 账面数量总和
          this.realStocksSum = data.goodsList
            .filter(obj => obj.real_stocks)
            .map(obj => obj.real_stocks)
            .reduce((total, next) => {
              return Number(total) + Number(next);
            }, 0); // 实盘数量总和
        } catch (error) {}
      } else {
        this.stocksSum = null;
        if (data.instoragList.company_id === 1) {
          this.$set(
            this.TableData.instoragList,
            "company_name",
            localStorage.getItem("company_name")
          );
        } else {
          let compay = this.companyList.filter(val => {
            return val.company_id == data.instoragList.company_id;
          });
          this.$set(
            this.TableData.instoragList,
            "company_name",
            compay[0].company_name
          );
        }
        let warehou = this.warehouseList.options.filter(val => {
          return val.warehouse_id == data.instoragList.warehouse_id;
        });
        if (!!this.TableData.instoragList.warehouse_id) {
          this.$set(
            this.TableData.instoragList,
            "warehouse_name",
            warehou[0].warehouse_name
          );
        }
        if (
          data.instoragList.keeper_id != undefined ||
          data.instoragList.keeper_id != null
        ) {
          let getEmp = this.getEmployeeList.options.filter(val => {
            return val.employee_id == data.instoragList.keeper_id;
          });
          this.$set(
            this.TableData.instoragList,
            "keeper_name",
            getEmp[0].employee_name
          );
        } else {
          this.$set(this.TableData.instoragList, "keeper_name", null);
        }
      }

      if (datalen < 32) {
        let dataThis = JSON.parse(JSON.stringify(data.goodsList));
        let diffNum = 32 - datalen;
        for (let i = 0; i <= diffNum; i++) {
          dataThis.push({
            zone: 0
          });
        }
        this.$set(this.TableData, "goodsList", dataThis);
      } else {
        this.$set(this.TableData, "goodsList", data.goodsList);
      }

      setTimeout(() => {
        _this.CreatePrintPages();
      }, 10);
    },
    printPdf(data) {
      this.list(data);
    },
    getRootPath() {
      var curPageUrl = window.document.location.href;
      var rootPath = curPageUrl.split("//")[1].split(".")[0];
      return rootPath;
    },
    CreatePrintPages() {
      var LODOP = getLodop();
      if (!LODOP) {
        return false;
      }
      var strStyle = LODOP.strStyle; //临时处理尺寸
      var URL = this.getRootPath();
      var LODOP = getLodop();
      var chicun = [];
      var top = [];

      LODOP.PRINT_INIT("");

      chicun = ["30mm", "80mm", "40mm", "5mm"];
      top = ["12mm", "40mm", "240mm", "280mm"];
      // LODOP.SET_PRINT_PAGESIZE(1, "205mm", "120mm", "CreateCustomPage");
      LODOP.SET_PRINT_PAGESIZE(1, 0, 0, "A4");
      // LODOP.SET_PRINT_PAGESIZE(1, "205mm", "93.3mm", "CreateCustomPage"); // 0 操作者自行决定或打印机缺省设置 1 纵向打印，固定纸张；2 横向打印，固定纸张
      LODOP.SET_PREVIEW_WINDOW(1, 0, 0, 1000, 600, ""); // 初始预览窗口大小
      LODOP.SET_SHOW_MODE("LANDSCAPE_DEFROTATED", 1); // 横向打印时正向显示
      LODOP.SET_SHOW_MODE("HIDE_PAPER_BOARD", 1); // 去除背景滚动线
      LODOP.SET_PRINT_MODE("AUTO_CLOSE_PREWINDOW", 1); // 打印后自动关闭预览
      LODOP.SET_PRINT_MODE("CUSTOM_TASK_NAME", "盘点单"); // 打印队列中的文档名
      // 追加打印头部
      LODOP.ADD_PRINT_HTM(
        top[0],
        "10mm",
        "185mm",
        chicun[0],
        document.getElementById("print-header").innerHTML
      );
      LODOP.SET_PRINT_STYLEA(0, "ItemType", 1);
      LODOP.SET_PRINT_STYLEA(0, "LinkedItem", 1);
      // 追加打印主体：分页、循环表格
      LODOP.ADD_PRINT_TABLE(
        top[1], //38
        "10mm",
        "185mm",
        chicun[1],
        document.getElementById("print-body").innerHTML
      );
      LODOP.SET_PRINT_STYLEA(0, "LinkedItem", 2);
      LODOP.SET_PRINT_STYLEA(0, "Vorient", 3);
      // 追加页码
      LODOP.ADD_PRINT_HTM(
        top[3],
        "10mm",
        "185mm",
        chicun[3],
        document.getElementById("print-pages").innerHTML
      );
      LODOP.SET_PRINT_STYLEA(0, "ItemType", 1);
      LODOP.PREVIEW();
    }
  },
  mounted() {
    getInventoryFinanceCompanyOpen(this, {
      type: 1
    });
    this.$common.options("getWarehouseListOption", this.warehouseList);
    this.$common.options("getEmployeeListOption", this.getEmployeeList); //业务员
  }
};
</script>
